.header {
  background-image: url(../images/header-bg-big.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__content {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    font-size: 36px;
    line-height: 36px;
    padding-bottom: 40px;

    @include on-tablet {
      padding-bottom: 65px;
      font-size: 48px;
      line-height: 48px;
    }

    @include on-desktop {
      padding-bottom: 92px;
      font-size: 56px;
      line-height: 56px;
    }
  }
}



